import { Select } from 'antd';

import layout from './fieldLayout';

export default function getSelectInstance() {
  return {
    Component: Select,
    label: 'Select',
    import: 'Select',
    extra: 'const { Option } = Select;',
    constructorCode: 'this.onChange = this.onChange.bind(this);',
    // 
    isField: true,
    fieldProps: {
      title: '',
      label: 'region',
      rules: [],
      ...layout,
    },
    methods: `
      onChange (val) {
        alert(val);
      }
    `,
    options: [{ label: '', value: 0 }, { label: '', value: 1 }],
    props: {
      style: { width: 200 },
      placeholder: '',
    },
  };
}



